import React, { memo, useMemo } from 'react'

import { getDictionaryByCode } from 'src/services'
import { commonConstants } from 'src/constants'

import { ReactComponent as TrafficIcon } from 'src/assets/images/icons/traffic-icon.svg'

import styles from './styles.module.scss'
const { RULES } = commonConstants

const TrafficRaw = ({ rules }) => {
  const renderRules = useMemo(() => {
    return rules.map((rule) => {
      return (
        <div className={styles.trafficItem} key={rule}>
          <div className={styles.trafficLabel}>{getDictionaryByCode(RULES, rule)}</div>
          <TrafficIcon className={styles.trafficIcon} />
        </div>
      )
    })
  }, [rules])

  return (
    <div className={styles.traffic}>
      <div className={styles.trafficHead}>Traffic rules</div>
      <div className={styles.trafficList}>{renderRules}</div>
    </div>
  )
}

export const Traffic = memo(TrafficRaw)
export default Traffic
