import React, { memo, useMemo, useEffect, useCallback } from 'react'

import { ReactComponent as DriveIcon } from 'src/assets/images/icons/drive.svg'
import { ReactComponent as ZoomIcon } from 'src/assets/images/icons/zoom.svg'

import { modalConstants } from 'src/constants'
import FullLoader from 'src/components/FullLoader'
import BaseButton from 'src/components/BaseButton'

import SmallGallery from '../SmallGallery'

import styles from './styles.module.scss'
import { useMappedActions, useMappedState } from './bindings'

const CreativesTabRaw = ({ offerId }) => {
  const { isLoading, data } = useMappedState()
  const actions = useMappedActions()

  useEffect(() => {
    actions.loadMedia(offerId)
  }, [actions, offerId])

  const openGallery = useCallback(
    (images) => {
      actions.showModal(modalConstants.GALLERY, { images })
    },
    [actions],
  )

  const renderItems = useMemo(() => {
    if (!data) {
      return null
    }

    return data.map((creative) => {
      return (
        <div className={styles.item} key={creative.id}>
          <div className={styles.left}>
            <div className={styles.imgBox}>
              <SmallGallery images={creative.files} />
              <button className={styles.openGallery} onClick={() => openGallery(creative.files)}>
                <ZoomIcon className={styles.zoom} />
              </button>
            </div>
          </div>
          <div className={styles.right}>
            <div className={styles.title}>{creative.title}</div>
            <div className={styles.descr}>{creative.description}</div>
            <div className={styles.action}>
              <BaseButton
                isLink
                text={
                  <span className={styles.innetBtn}>
                    <span className={styles.innetBtnText}>Download from Google Drive</span>
                    <DriveIcon className={styles.innetBtnIcon} />
                  </span>
                }
                href={creative.link}
                skin="yellow"
                disabled={false}
              />
            </div>
          </div>
        </div>
      )
    })
  }, [data, openGallery])

  if (isLoading) {
    return (
      <div className={styles.body}>
        <FullLoader />
      </div>
    )
  }

  return <div className={styles.body}>{renderItems}</div>
}

export const CreativesTab = memo(CreativesTabRaw)
export default CreativesTab
