import React, { memo, useMemo } from 'react'

import styles from './styles.module.scss'

const SmallGalleryRaw = ({ images }) => {
  const renderImage = useMemo(() => {
    return images.map((image, i) => {
      return (
        <div className={styles.galleryItem} key={i}>
          <div className={styles.galleryItemPreview}>
            <div
              className={styles.galleryItemImage}
              style={{
                backgroundImage: `url(${image.link})`,
              }}
            ></div>
          </div>
        </div>
      )
    })
  }, [images])

  return <div className={styles.gallery}>{renderImage}</div>
}

export const SmallGallery = memo(SmallGalleryRaw)
export default SmallGallery
