import React, { memo, useMemo } from 'react'
import cn from 'classnames'

import styles from './styles.module.scss'

const TabsRaw = ({ tabs, activeTab, setActiveTab }) => {
  const renderTabs = useMemo(() => {
    return tabs.map((tab) => {
      return (
        <div
          className={cn(styles.tabItem, {
            [styles.tabItemActive]: activeTab.id === tab.id,
          })}
          onClick={() => {
            setActiveTab(tab)
          }}
          key={tab.id}
        >
          {tab.name}
        </div>
      )
    })
  }, [tabs, activeTab, setActiveTab])

  return <div className={styles.tabs}>{renderTabs}</div>
}

export const Tabs = memo(TabsRaw)
export default Tabs
