import { useMapActions, useMapState } from 'src/hooks'
import { offerThunks, modalThunks } from 'src/thunks'
import { offerSelectors } from 'src/selectors'

export const useMappedState = () => {
  return useMapState((state) => {
    return {
      data: offerSelectors.getMediaData(state),
      isLoading: offerSelectors.getMediaIsLoading(state),
    }
  })
}

export const useMappedActions = () => {
  return useMapActions({
    loadMedia: offerThunks.loadMedia,
    showModal: modalThunks.showModal,
  })
}
