import React, { memo } from 'react'
import cn from 'classnames'

import { getDictionaryByCode } from 'src/services'
import { commonConstants } from 'src/constants'

import styles from './styles.module.scss'

const BrandRaw = ({ image, metrics, reward }) => {
  const { METRICS } = commonConstants

  return (
    <div className={styles.brand}>
      <div className={styles.brandLeft}>
        <div className={styles.logo}>
          <img src={image} alt="" />
        </div>
      </div>
      <div className={styles.brandRight}>
        <div className={styles.price}>
          <div className={styles.priceLabel}>CPL:</div>
          <div className={styles.priceValue}>
            {reward.amount} {reward.currency}
          </div>
        </div>

        <div className={styles.metrics}>
          <div className={styles.metricItem}>
            <div className={styles.metricLabel}>{getDictionaryByCode(METRICS, 'EPC')}</div>
            <div className={styles.metricValue}>{metrics.EPC}</div>
          </div>
          <div className={styles.metricItem}>
            <div className={styles.metricLabel}>{getDictionaryByCode(METRICS, 'EPL')}</div>
            <div className={cn(styles.metricValue, styles.metricValueGray)}>{metrics.EPL}</div>
          </div>
        </div>
      </div>
    </div>
  )
}

export const Brand = memo(BrandRaw)
export default Brand
