import React, { memo } from 'react'

import FullLoader from 'src/components/FullLoader'

import Brand from '../Brand'
import Traffic from '../Traffic'
import Generate from '../Generate'
import Static from '../Static'

import styles from './styles.module.scss'

const DescriptionTabRaw = ({ id, info, isLoading, rules, url, metrics, reward, loadLink, isManager }) => {
  if (isLoading || !id) {
    return (
      <div className={styles.body}>
        <FullLoader />
      </div>
    )
  }

  return (
    <div className={styles.body}>
      <div className={styles.left}>
        <div className={styles.info}>
          <Brand image={info.logo} reward={reward} metrics={metrics} />
          {url && <Generate url={url} status={'none'} getlink={loadLink} offerId={id} isManager={isManager} />}
        </div>
        <Static content={info.description} />
      </div>
      <div className={styles.right}>
        <Traffic rules={rules} />
      </div>
    </div>
  )
}

export const DescriptionTab = memo(DescriptionTabRaw)
export default DescriptionTab
