import React, { memo, useEffect, useMemo, useState } from 'react'

import PageLayout from 'src/components/PageLayout'

import Tabs from './components/Tabs'
import DescriptionTab from './components/DescriptionTab'
import CreativesTab from './components/CreativesTab'

import styles from './styles.module.scss'
import { useMappedActions, useMappedState } from './bindings'

const TABS = [
  { id: 'DESCRIPTION', name: 'Description' },
  { id: 'CREATIVES', name: 'Creatives' },
]

const OfferRaw = ({ match, location }) => {
  const [activeTab, setActiveTab] = useState(TABS[0])
  const actions = useMappedActions()
  const { data, isManager } = useMappedState()
  const offerId = match.params.offerId

  const pageTitle = useMemo(() => {
    if (data && data.info) {
      return data.info.title
    }
    if (location && location.state && location.state.title) {
      return location.state.title
    }
    return ''
  }, [data, location])

  useEffect(() => {
    actions.loadOffer(offerId)
  }, [offerId, actions])

  const renderTab = useMemo(() => {
    switch (activeTab.id) {
      case 'DESCRIPTION': {
        return <DescriptionTab loadLink={actions.loadLink} {...data} isManager={isManager} />
      }
      case 'CREATIVES': {
        return <CreativesTab offerId={offerId} />
      }

      default: {
        return null
      }
    }
  }, [activeTab, offerId, data, actions, isManager])

  return (
    <PageLayout pageTitle={pageTitle} backRoute={'/offers'}>
      <div className={styles.page}>
        <div className={styles.head}>
          <Tabs tabs={TABS} activeTab={activeTab} setActiveTab={setActiveTab} />
        </div>
        {renderTab}
      </div>
    </PageLayout>
  )
}

export const Offer = memo(OfferRaw)
export default Offer
