import React, { memo, useRef, useMemo, useState, useCallback } from 'react'
import cn from 'classnames'

import BaseButton from 'src/components/BaseButton'
import { ReactComponent as TrafficIcon } from 'src/assets/images/icons/traffic-icon.svg'
import { ReactComponent as CheckboxIcon } from 'src/assets/images/icons/checkbox.svg'
import { ReactComponent as LoaderIcon } from 'src/assets/images/icons/link_loader.svg'

import styles from './styles.module.scss'

const GenerateRaw = ({ url, offerId, getlink, isManager }) => {
  const inputRef = useRef(null)
  const [status, setStatus] = useState('none')
  const isDisableBtn = ['loading', 'success', 'denied'].includes(status)

  const copyLink = () => {
    if (inputRef.current) {
      inputRef.current.select()
      inputRef.current.setSelectionRange(0, 99999) /* For mobile devices */
      document.execCommand('copy')
    }
  }

  const onClickGenerate = useCallback(() => {
    setStatus('loading')

    setTimeout(() => {
      // getlink(offerId)
      setStatus('success')
    }, 500)
  }, [])

  const renderStatus = useMemo(() => {
    switch (status) {
      case 'none': {
        return (
          <div className={cn(styles.generateStatus, styles.generateStatusLabel)}>
            Click on the button for your personal link to appear here
          </div>
        )
      }
      case 'loading': {
        return (
          <div className={cn(styles.generateStatus, styles.generateStatusLoader)}>
            <LoaderIcon stroke="#00A389" className={cn(styles.generateIcon, styles.generateStatusLoader)} />
            An offer request has been sent to the administrator. It will be processed shortly. You will receive the
            answer in Support.
          </div>
        )
      }
      case 'success': {
        return (
          <div className={styles.generateStatus}>
            <CheckboxIcon stroke="#00A389" className={styles.generateIcon} />
            Access to the link is approved by the administrator
          </div>
        )
      }
      case 'denied': {
        return (
          <div className={styles.generateStatus}>
            <TrafficIcon className={styles.generateIcon} /> Access to the link is not approved by the administrator
          </div>
        )
      }
      case 'error': {
        return (
          <div className={styles.generateStatus}>
            <TrafficIcon className={styles.generateIcon} /> Something went wrong
          </div>
        )
      }

      default: {
        return <div className={styles.generateStatus}></div>
      }
    }
  }, [status])

  if (isManager) {
    return (
      <div className={styles.generate}>
        <div className={styles.generateBottom}>
          <div className={styles.generateLabel}>Link to brand:</div>
          <div className={styles.generateLink}>
            <input className={styles.generateInput} type="text" value={url} ref={inputRef} readOnly />
          </div>
          <button onClick={copyLink} className={styles.generateCopy}>
            Copy link
          </button>
        </div>
      </div>
    )
  }

  return (
    <div className={styles.generate}>
      <div className={styles.generateTop}>
        <div className={styles.generateBtn}>
          <BaseButton text="Generate link" skin="yellow" disabled={isDisableBtn} onClick={onClickGenerate} />
        </div>
        {renderStatus}
      </div>
      {status === 'success' && (
        <div className={styles.generateBottom}>
          <div className={styles.generateLink}>
            <input className={styles.generateInput} type="text" value={url} ref={inputRef} readOnly />
          </div>
          <button onClick={copyLink} className={styles.generateCopy}>
            Copy link
          </button>
        </div>
      )}
    </div>
  )
}

export const Generate = memo(GenerateRaw)
export default Generate
